#about {
	.container-new {
		@include md {
			padding-top: 20px;
			padding-bottom: 20px; } }
	.row {
		@include sm {
			text-align: center; } }
	h2 {
		@include sm {
			font-size: 20px; } }
	hr {
		@include sm {
			margin-top: 20px;
			margin-bottom: 10px; } }
	.news-detail {
		@include md {
			text-align: center; } } }

.about-block {
	img {
		@include lg {
			width: 29.53%; }
		@include sm {
			width: 40%;
			float: none; } } }
.about-left {
	@include lg {
		width: 70.47%;
		margin-left: 0;
		padding-left: 45px; }
	@include sm {
		width: 100%;
		float: none;
		padding-left: 0; }
	p {
		@include sm {
			font-size: 16px;
			margin-bottom: 15px; } } }
.prod-block {
	@include lg {
		width: 33%;
		padding: 0.985% 0.985% 0;
		margin: 0;
		vertical-align: top; }
	@include md {
		width: 49.7%; }
	@include sm {
		width: 100%; }
	img {
		@include lg {
			display: block;
			max-width: 100%;
			height: 436px;
			margin: 0 auto;
			object-fit: cover; }
		@include md {
			object-position: top; } }
	p {
		@include md {
			margin-bottom: 15px;
			margin-top: 7px;
			font-size: 14px; } } }
