body {
	min-width: 320px;
	&.open {
		position: fixed;
		left: 0;
		top: 0;
		width: 100vw;
		height: 100vh; }
	@include lg {
		padding: 0 15px; }
	@include md {
		font-size: 13px; } }

h1 {
	@include sm {
		font-size: 20px;
		margin: 0 0 10px 0; } }

.container-p {
	@include md {
		padding: 0 45px; }
	@include sm {
		padding: 0 15px; } }

.container-new {
	@include lg {
		max-width: 1200px;
		width: 100%; } }
