.movie-block {
	height: auto;
	@include lg {
		height: 100%;
		margin: 0 20px; }
	@include sm {
		width: 180px !important; } }
.movie-block:last-child {
	@include lg {
		margin-right: 0; } }
.movie-block:first-child {
	@include lg {
		margin-left: 0; } }
.movie-block p {
	margin-bottom: 30px;
	@include md {
		font-size: 14px; } }
#movies {
	.row.slick-slider {
		height: 190px;
		@include lg {
			height: auto; } }
	.container-new {
		@include md {
			padding-top: 20px;
			padding-bottom: 20px; } }
	.slick-slide {
		img {
			max-width: 100%;
			margin: 0 auto;
			@include sm {
				width: 100%; } } }
	.slick-dots {
		margin: 0 auto;
		li {
			display: inline-block;
			margin: 0 10px;
			button {
				border-radius: 50%;
				font-size: 0;
				width: 10px;
				height: 10px;
				padding: 0;
				background: #AEB4C0; } }
		.slick-active {
			button {
				background: #FBFFFC;
				padding: 0; } } }
	ul {
		-webkit-padding-start: 0px !important;
		padding: 0; } }

