#contacts {
	.container-new {
		@include md {
			padding-top: 20px;
			padding-bottom: 20px; } }
	h2 {
		@include sm {
			font-size: 20px;
			margin: 0 0 10px 0; } } }

.contacts-left {
	@include md {
		float: none;
		text-align: center;
		margin: 0 auto; }
	@include sm {
		width: 100%; }
	h3 {
		@include sm {
			margin-top: 10px;
			font-size: 18px; } }
	p {
		@include md {
			width: auto; }
		@include sm {
			font-size: 14px; } } }

.contacts-social {
	@include md {
		margin-top: 30px; } }

.map {
	@include lg {
		width: 100%;
		max-width: calc(100% - 300px);
		height: 440px; }
	@include md {
		height: 310px;
		float: none;
		margin: 15px auto 0;
		max-width: 700px; }
	@include sm {
		border: 2px #fff solid;
		height: 304px; }
	#ymaps1528727547522448162 {
		width: 100%; } }

.map > ymaps,
.map > ymaps > ymaps {
	@include lg {
		width: 100% !important;
		height: 430px !important; }
	@include md {
		height: 300px !important; } }


