footer {
	@include lg {
		margin: 0 -15px;
		padding: 0 15px; }
	@include md {
		text-align: center; }
	.container-new {
		@include md {
			padding: 20px 45px; }
		@include sm {
			padding: 15px; } }
	h2 {
		@include md {
			margin-bottom: 15px;
			font-size: 16px; } }
	.row {
		@include lg {
			display: flex;
			justify-content: space-between;
			flex-wrap: wrap; }
		@include md {
			display: block; }
		&:before {
			@include lg {
				display: none; } }
		&:after {
			@include lg {
				display: none; } } }
	.footer-c {
		@include lg {
			margin-left: 0;
			float: none;
			padding-right: 10px;
			padding-left: 65px; }
		@include md {
			margin-bottom: 25px;
			padding: 0; } }
	.footer-l {
		@include lg {
			float: none; }
		@include md {
			margin-bottom: 25px; } }
	.footer-r {
		@include lg {
			float: none; }
		@include md {
			margin-bottom: 25px; } } }

#copy {
	p {
		@include lg {
			line-height: 1.3;
			padding: 17px 0; }
		@include md {
			font-size: 12px;
			text-align: center; } } }


