div.news-detail {
	img.detail_picture {
		@include md {
			float: none; } } }

.news-detail {
	h3 {
		@include sm {
			font-size: 20px; } } }

.prod-who {
	p {
		@include sm {
			font-size: 15px; } } }

.news-date-time {
	@include md {
		display: block; } }

#about-prod {
	table {
		@include md {
			width: 100%; }
		& td:first-child {
			@include md {
				width: 50%; }
			@include sm {
				display: block;
				width: 100%;
				padding-right: 0; } }
		& td:last-child {
			@include md {
				width: 50%; }
			@include sm {
				display: block;
				width: 100%; } } }
	.gallery {
		@include lg {
			text-align: center; } }
	.detail_picture {
		@include md {
			margin-right: 0; }
		@include sm {
			max-width: 100%;
			object-fit: cover; } } }

#about > .container-new > p > a {
	@include md {
		padding: 13px 45px;
		font-size: 16px;
		display: block; }
	@include sm {
		width: 100%; } }

#about > .container-new > p {
	@include sm {
		width: 100%;
		margin: 15px auto 0; } }
