.center-movie {
	@include lg {
		width: 32.515%;
		margin-left: 0;
		padding-left: 40px;
		height: auto; }
	@include md {
		float: none;
		width: 100%;
		padding: 0;
		text-align: center; }
	a {
		@include lg {
			position: relative;
			width: 100%;
			bottom: 5px; }
		@include md {
			display: block; } }
	a:last-child {
		@include lg {
			position: relative; } }
	h3 {
		@include sm {
			font-size: 18px; } }
	p {
		@include md {
			text-align: center !important; } } }
.right-movie {
	@include lg {
		width: 36.4%;
		margin-left: 0;
		padding-left: 20px;
		height: auto; }
	@include md {
		float: none;
		padding: 0;
		width: 100%;
		text-align: center; }
	a:last-child {
		@include lg {
			display: block;
			width: 100%;
			position: relative; } }
	h4 {
		@include md {
			margin-top: 10px;
			margin-bottom: 10px; } }
	p {
		@include md {
			margin-bottom: 10px; } } }

#movies {
	.movie-wrap {
		@include sm {
			margin-bottom: 15px; }
		a:first-child {
			@include lg {
				display: block;
				width: 30.31%; }
			@include md {
				margin: 0 auto; }
			@include sm {
				width: 50%; } }
		a {
			img {
				@include lg {
					width: 100%;
					display: block; }
				@include md {
					float: none; } } } }
	.btn-trailer {
		@include lg {
			// font-size: 15px
			width: 100%;
			padding: 9px; }
		@include md {
			margin-top: 15px; } }
	.btn-movie {
		@include lg {
			margin-top: 0;
			width: 100%;
			padding: 25px 20px;
			line-height: 1.2; }
		@include md {
			padding: 13px 10px;
			font-size: 16px; } }
	h5 {
		@include md {
			text-align: center; } } }

.movie-descr {
	p {
		@include md {
			margin-top: 25px;
			width: 100%; } } }
.movie-genre {
	p {
		@include lg {
			margin-bottom: 27px; }
		@include md {
			margin-top: 20px;
			margin-bottom: 15px; } } }

.movie-wrap {
	img {
		@include lg {
			width: 30.31%; }
		@include md {
			float: none; }
		@include sm {
			width: 50%; } } }

.modal {
	display: block;
	visibility: hidden;
	opacity: 0;
	@include md {
		@include vertical; }
	@include sm {
		background-color: #1A1A1A; }
	&.in {
		opacity: 1;
		visibility: visible;
		.modal-dialog {
			@include md {
				display: inline-block;
				vertical-align: middle;
				margin: 0; }
			@include sm {
				width: 96%;
				padding-top: 28px; } } }
	.close {
		@include sm {
			margin-top: -25px;
			margin-right: 0;
			position: relative;
			z-index: 1000; }
		img {
			@include sm {
				width: 20px; } } }
	.modal-body {
		@include md {
			padding: 0; } }
	.modal-body > div {
		@include md {
			margin: 0 auto; } } }


.img-block {
	@include sm {
		img {
			border: 2px #fff solid; } } }
