header {
	min-width: 290px;
	overflow: hidden;
	@include md {
		min-width: 290px;
		height: 400px; }
	.bg-img {
		@include md {
			height: 400px; }
		@include sm {
			height: 290px; }
		@include r(450) {
			height: 200px; } }
	.container-new {
		@include md {
			height: 400px; }
		@include sm {
			height: 290px; }
		@include r(450) {
			height: 200px; } }
	.container-new, .owl-carousel, .owl-carousel .owl-stage-outer {
		@include md {
			height: 400px; }
		@include sm {
			height: 290px; }
		@include r(450) {
			height: 200px; } }
	@include sm {
		height: 290px; }
	@include r(450) {
		height: 200px; } }

.navbar-collapse {
	@include md {
		text-align: center;
		width: 100%;
		overflow: auto;
		position: absolute;
		height: 100vh;
		// right: -200px
		top: 55px;
		padding: 20px 30px;
		transition: opacity 0.5s;
		opacity: 0;
		visibility: hidden;
		&.is-open {
			opacity: 1;
			visibility: visible; } } }

.navbar-brand {
	@include md {
		margin-left: 30px; }
	img {
		@include md {
			width: 45px; } } }

.pull-left, .pull-right {
	@include md {
		float: none !important; } }

.navbar-nav {
	@include md {
		margin: 0;
		padding-top: 0;
		h1 {
			display: none; } }
	@include md {
		li {
			float: none;
			a {
				margin: 0;
				padding: 6px 0 10px 0; }
			span {
				display: none; } } } }

ul li a.selected, ul li a:hover {
	@include md {
		padding-bottom: 8px; } }

.nav-right {
	@include md {
		text-align: center;
		margin-right: 0;
		display: flex;
		justify-content: center;
		flex-direction: column-reverse;
		padding-top: 10px; } }

.menu-mobile-cross {
	display: none;
	@include md {
		display: inline-block;
		position: absolute;
		top: 19px;
		right: 30px;
		width: 25px;
		height: 33px;
		span {
			position: absolute;
			top: 6px;
			width: 25px;
			height: 2px;
			margin-bottom: 10px;
			background-color: #fff;
			cursor: pointer;
			opacity: 1;
			visibility: visible;
 }			// transition: opacity 0.3s
		&:before {
			content: '';
			position: absolute;
			top: 0;
			width: 25px;
			height: 2px;
			margin-bottom: 10px;
			background-color: #fff;
			cursor: pointer;
			transition: transform 0.5s; }
		&:after {
			content: '';
			position: absolute;
			top: 12px;
			width: 25px;
			height: 2px;
			margin-bottom: 10px;
			background-color: #fff;
			cursor: pointer;
			transition: transform 0.5s; }
		&.is-open {
			&:before {
				transform: rotate(-45deg);
				top: 5px; }
			&:after {
				top: 5px;
				transform: rotate(45deg); }
			span {
				// top: 10px
				visibility: hidden;
				opacity: 0;
				top: 6px; } } } }


.navbar {
	@include lg {
		margin: 0 -15px; }
	@include md {
		height: 55px; } }

.owl-carousel {
	.owl-item {
		img {
			@include lg {
				object-fit: cover;
				object-position: 50% 50%;
				height: 500px; }
			@include md {
				height: 400px; }
			@include sm {
				height: 290px; }
			@include r(450) {
				height: 200px; } } } }
