#news {
	.container-new {
		@include md {
			padding-top: 20px;
			padding-bottom: 20px; } } }

.big-row {
	@include md {
		margin-bottom: 0; }
	.news-left {
		@include lg {
			width: calc(100% - 341px); }
		@include md {
			width: 100%;
			max-width: 100%;
			margin-bottom: 10px; }
		@include sm {
			height: 400px; }
		@include r(450) {
			height: 300px; } }
	.news-right {
		@include md {
			display: flex;
			width: 100%;
			flex-direction: column; } }
	.news-right-top {
		@include md {
			width: 100%; }
		@include sm {
			height: auto; }
		img {
			@include lg {
				object-fit: cover;
				left: 50% !important;
				transform: translateX(-50%); }
			@include md {
				object-fit: cover;
				width: 100%;
				position: relative;
				height: 100%; } } }
	.news-right-bottom {
		@include md {
			width: 100%;
			margin-bottom: 10px;
			padding: 10px 10px 39px;
			height: auto; }
		h3 {
			@include lg {
				line-height: 1.5; }
			@include sm {
				font-size: 16px; } }
		p {
			@include md {
				margin: 10px 0; } } }
	.xoverlay-data {
		@include sm {
			padding: 10px 15px; }
		h3 {
			@include sm {
				font-size: 14px; } } } }
.date {
	@include md {
		p {
			margin-top: 0; } } }

.x-right {
	&:hover {
		@include lg {
			.xoverlay-box {
				height: auto; } } }
	.xoverlay-box {
		right: -100%;
		@include lg {
			height: auto; }
		@include sm {
			right: 0; } } }
.xoverlay {
	@include md {
		height: 100%; }
	@include sm {
		.x-img-main {
			height: auto; } } }

.small-row {
	@include md {
		margin-bottom: 0;
		display: flex;
		flex-direction: column-reverse; }
	.news-left {
		@include md {
			width: 100%;
			margin-bottom: 10px;
			padding: 10px 10px 39px;
			height: auto; }
		h3 {
			@include lg {
				line-height: 1.3;
				width: 100%; }
			@include sm {
				font-size: 16px; } }
		p {
			@include md {
				margin: 10px 0; } } }
	.news-right {
		@include lg {
			width: calc(100% - 354px); }
		@include md {
			width: 100%;
			margin-bottom: 10px; }
		@include sm {
			height: auto; }
		img {
			@include lg {
				object-fit: cover;
				left: 50% !important;
				transform: translateX(-50%); }
			@include md {
				width: 100%;
				position: relative;
				height: 100%; } } } }

.btn-news {
	@include lg {
		max-width: 390px;
		width: 100%;
		padding: 30px 10px;
		line-height: 1.1; }
	@include md {
		font-size: 16px;
		padding: 13px 10px;
		margin-top: 15px; }
	@include sm {
		max-width: 100%; } }

.btn-prev {
	@include md {
		margin: 15px 0 0; } }

.btn-next {
	@include md {
		margin-top: 15px; } }


