.press-header-left {
	position: relative;
	@include sm {
		float: none; }
	input {
		@include sm {
			float: none;
			width: 100%;
			border: 1px solid #ffffff; } } }
.btn-search {
	@include sm {
		margin-left: 0;
		margin-top: 2px;
		font-size: 0;
		position: absolute;
		right: 1px;
		top: 7px;
		background-color: transparent;
		line-height: 0;
		width: 30px; }
	img {
		@include sm {
			width: 15px; } } }

.press-block-img {
	@include sm {
		float: none;
		width: 100%;
		margin-right: 0; } }

.btn-press-block {
	@include sm {
		position: relative;
		margin-top: 10px;
		left: 0; } }

.btn-back-to-top {
	@include sm {
		float: none;
		width: 100%;
		margin: 20px 0 15px;
		font-size: 11px; } }

.btn-press-more {
	@include sm {
		float: none;
		width: 100%;
		margin: 0;
		font-size: 11px; }
	img {
		@include sm {
			margin: 0 4px;
			width: 10px; } } }
