#single-news {
	.x-img-main {
		@include lg {
			display: block;
			max-width: 100%;
			height: auto !important;
			margin: 0 auto; } }
	h2 {
		@include sm {
			font-size: 20px;
			margin-bottom: 10px; } }
	h3 {
		@include md {
			text-align: center; }
		@include sm {
			margin-top: 15px;
			margin-bottom: 10px;
			font-size: 20px; } }
	h4 {
		@include md {
			text-align: center; } }
	iframe {
		@include sm {
			width: 100%; } }
	p {
		img {
			@include lg {
				display: block;
				max-width: 100%;
				margin: 0 auto;
				height: auto !important; } } } }

.gallery {
	.img-block:first-child, .img-block:nth-child(5) {
		margin-left: 10px; } }
